const SENDMESSAGE = "disalogs/SEND_MESSAGE";
const initialState = {
  dialogs: [
    { id: 1, name: "Egor" },
    { id: 2, name: "Max" },
    { id: 3, name: "Alla" },
    { id: 4, name: "Ekaterina" },
  ],
  messages: [
    { author: "Alice", message: "Hello", id: 1 },
    { author: "Andrew", message: "I'd like to do that", id: 2 },
    { author: "Chris", message: "Wow, you look awesome", id: 3 },
  ],
};
const dialogsReducer = (state = initialState, action) => {
  switch(action.type) {
    case SENDMESSAGE:
    return {
      ...state,
      messages: [
        ...state.messages,
        { author: "Alice", message: action.messageText, id: 1 },
      ],
    };
  }
  return state;
};

export const sendNewMessage = (messageText) => {
  return { type: SENDMESSAGE, messageText};
};
export default dialogsReducer;
