import { getAuth } from "./auth-reducer";
const SUCCESS_USER_AUTH = 'auth/SUCCESS_USER_AUTH'
const initialState = {
    initialized: false,
}
const appReducer = (state = initialState, action) =>{
    switch(action.type){
        case SUCCESS_USER_AUTH:
          return{...state,
             initialized: true}
        default:
          console.log(state)
          return state
    }}
export const authUserSucces = () => {
    return {type: SUCCESS_USER_AUTH}
}

export const getIsAuthInfo = () => async(dispatch) => {
  const promise = dispatch(getAuth())
  await Promise.all([promise])
  dispatch(authUserSucces())
}

export default appReducer
