import React from "react";
import "./App.css";
import { ApiHeaderContainer } from "./components/Header/HeaderContainer";
import SideBar from "./components/SideBar/SideBar";
import { BrowserRouter, Route, Routes, HashRouter } from "react-router-dom";
import { connect } from "react-redux";
import { getIsAuthInfo } from "./redux/app-reducer";
import Preloader from "./components/commons/Preloader";
import store from "./redux/redux-store";
import { Provider } from "react-redux";
import createSuspense from "./components/HOCS/withSuspense";
const Login = React.lazy(()=>import("./components/Login/Login"))
const UsersContainer = React.lazy(()=>import('./components/Users/UsersContainer'))
const ProfileContainer = React.lazy(()=>import('./components/Profile/ProfileContainer'))
const DialogsContainer = React.lazy(()=>import('./components/Dialogs/DialogsContainer'))
 class App extends React.Component{
 
  componentDidMount(){
    this.props.getIsAuthInfo()
  }
   render(){
     console.log(1)
     if(!this.props.initialized){
       return <Preloader />
     }
    return (
      <HashRouter>
        
        <div className="app-wrapper">
          <ApiHeaderContainer />
          <SideBar />
          <div className="app-wrapper-content">
            <Routes>
              <Route path='/login' element={createSuspense(Login)}/>
              <Route path="/users" element={createSuspense(UsersContainer)}/>
              <Route path="/profile/*" element={createSuspense(ProfileContainer)}/>
              <Route path="/dialogs/*" element={createSuspense(DialogsContainer)} />
            </Routes>
          </div>
        </div>
      </HashRouter>)
   }
}
const mapStateToProps=(state)=>{
  return{
    initialized: state.app.initialized
  }
}
const AppWithRoutes= connect(mapStateToProps, {getIsAuthInfo})(App);
const MainApp = ()=>{
 return <Provider store={store}>
  <AppWithRoutes/>
</Provider>
}
export default MainApp
