import { usersApi } from "../api/api";
import updateObjectInArray from "../components/commons/helpers/updateArray";
const SHOWMORE = 'users/SHOW_MORE';
const SETCURRENTPAGE = 'users/SET_CURRENT_PAGE';
const SETPAGES = 'users/SET_PAGES';
const PRELOADING = 'users/PRELOADING';
const FOLLOWINGINPROGRESS = 'users/FOLLOWING_IN_ROGRESS';
const FOLLOWING = 'users/FOLLOWING';
const initialState = {
    users:[],
    pageSize: 5,
    totalUsersCount: 5,
    currentPage: 2,
    isPreloading: false,
    isFollowProgressing: [],
}
export const usersReducer = (state = initialState, action) => {
  switch(action.type){
    case FOLLOWING:
        return{
            ...state, users: updateObjectInArray(state.users, action.userId, 'id', {followed: action.value})
        }
    case SETCURRENTPAGE: return{...state, currentPage: action.pageNumber}
    case SHOWMORE: return{...state, users: [...action.newUsers]}
    case PRELOADING: return{...state, isPreloading: action.value}
    case SETPAGES:return{...state, totalUsersCount: action.usersCount}
    case FOLLOWINGINPROGRESS: return {...state, 
    isFollowProgressing: action.value ? 
    [...state.isFollowProgressing, action.userId] : 
    [state.isFollowProgressing.filter
        (userId => userId !== action.userId )]}
    default:return state}
    }
const following = (userId, value)=>{
    return{type: FOLLOWING, userId, value}
}
const showMoreUsers = (newUsers)=>{
    return{type:SHOWMORE, newUsers}}
export const setCurrentPage = (pageNumber)=>{
    return{type: SETCURRENTPAGE, pageNumber}}
const setTotalUsersCount =(usersCount)=>{
    return{type: SETPAGES, usersCount}}
const usePreloding = (value) => {
    return{type: PRELOADING, value}}
export const followingInProgress = (value, userId) =>{
    return{type: FOLLOWINGINPROGRESS, value, userId}}
export default usersReducer
export const getUsers = (currentPage) =>async(dispatch)=>{
        dispatch(usePreloding(true))
        const data = await usersApi.getUsers(currentPage, 5)
        dispatch(usePreloding(false))
        dispatch(showMoreUsers(data.items))
        dispatch(setTotalUsersCount(Math.round(data.totalCount)))
    }
const doFollow = async(method,userId,value, dispatch) =>{
    dispatch(followingInProgress(true, userId))
    const resultCode = await method(userId)
    if(resultCode === 0){
        dispatch(following(userId, value))
        dispatch(followingInProgress(false, userId))}
}
export const follow = (userId) =>(dispatch)=>{
       doFollow(usersApi.follow.bind(usersApi), userId, true, dispatch)
    }
    
export const unfollow = (userId) => (dispatch)=>{
        doFollow(usersApi.unfollow.bind(usersApi), userId, false, dispatch)
}
    