import axios from "axios";
const follow = 'follow/'
const instance = axios.create({
  baseURL:'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers:{"API-KEY":"def39472-0edd-449a-95e0-f272b2339d3b"},
})
export const usersApi = {
  getUsers(currentPage = 1, totalUsersCount = 10){
    return instance.get(`users?page=${currentPage}&count=${totalUsersCount}`)
       .then((res) => res.data)},
  getProfile(path){
    console.warn('Old method, use profileApi');
    return profileApi.getProfile(path)
  },
  follow(userId){return instance.post(follow + userId).then(res=>res.data.resultCode)},
  unfollow(userId){return instance.delete(follow + userId).then(res=>res.data.resultCode)},
}
export const profileApi = {
  getProfile(path){return instance.get(`profile/${path}`)},
  getStatus(userId){return instance.get(`profile/status/${userId}`)},
  updateStatus(status){return instance.put('profile/status', {status:status})}
}
export const authApi = {
  getUserAuth(){return instance.get('auth/me')},
  login(email,password, rememberMe){return instance.post('/auth/login', {email,password, rememberMe})},
  logout(){return instance.delete('/auth/login')}
}


