import { usersApi } from "../api/api";
import { profileApi } from "../api/api";
const ADDPOST = "profile/add-post";
const SETUSERSPROFILE = "profile/set-users-profile";
const SETSTATUS = "profile/set-status";
const DELETEPOST = "profile/delete-post"
export const initialState = {
  userInfo: null,
  status: '',
  posts: [
    {id: 1,uName: "Stiv",uLName: "Jobs",ttl: "Heya",mssg: "I'd like to discover this world",},
    {id: 2,uName: "Darth",uLName: "Vader",ttl: "Hello there",mssg: "Uwu, i like star wars so much",},
    { id: 3, uName: "Felix", uLName: "Kenobi", ttl: "Kolbasa", mssg: "WAAASa" },
  ],
};
const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADDPOST:
      return {...state,
        posts: [...state.posts,
          {
            uName: "User",
            uLname: "User",
            ttl: "User Title",
            id: 4,
            mssg: action.postText,
          },
        ],
      };
    case SETUSERSPROFILE:
      return {
        ...state,
        userInfo: action.userInfo,
      };
    case SETSTATUS:
      return { ...state, status: action.status };
    case DELETEPOST:
      return{...state, posts: state.posts.filter(p=>p.id !== action.postId)};
    default:
      return state;
  }
};
export const addPostAC = (postText) => {
  return { type: ADDPOST, postText };
};
export const deletePostAC = (postId)=>{
  return {type: DELETEPOST, postId}
}
export const setUserProfile = (userInfo) => {
  return { type: SETUSERSPROFILE, userInfo };
};
export const setUserStatus = (status) => {
  return { type: SETSTATUS, status };
};

export const getUserStatus = (userId) => async(dispatch)=>{
  const res = await profileApi.getStatus(userId)
  dispatch(setUserStatus(res.data));
};
export const updateStatus = (status) => async(dispatch)=>{
  const res = await profileApi.updateStatus(status)
  if(res.data.resultCode === 0){
    dispatch(setUserStatus(status))
  }
};
export const getUserProfile = (path) => async(dispatch)=>{
    const res = await usersApi.getProfile(path)
    dispatch(setUserProfile(res.data))
};
export default profileReducer;
