import { stopSubmit } from "redux-form"
import { authApi } from "../api/api"
const SETUSERDATA = 'auth/SET_USER_DATA'
const initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
}
const authReducer = (state = initialState, action) =>{
    switch(action.type){
        case SETUSERDATA:
          return{...state,
             ...action.data,
            isAuth: action.data.value}

        default:
          return state
    }}
export const setAuthUserData = (userId, email, login, value) => {
    return { type: SETUSERDATA, data:{userId, email, login, value}}}

export const getAuth = () => async(dispatch) => {
  const res = await authApi.getUserAuth()
  if(res.data.resultCode === 0){
  const {id, email, login} = res.data.data;
  dispatch(setAuthUserData(id,email,login, true))}
}
export const login = (email, password, rememberMe) => async(dispatch) =>{
  const res = await authApi.login(email,password, rememberMe)
  console.log(res)
        if(res.data.resultCode === 0){
          dispatch(getAuth())
        } else{
            const errors = res.data.messages.length > 0 ? res.data.messages[0] : 'somw error'
            console.log(errors)
            dispatch(stopSubmit('login', {_error: errors}))
        }
}
export const loGout = () => async(dispatch) =>{
   const res = await authApi.logout()
        if(res.data.resultCode === 0){
            dispatch(setAuthUserData(null,null,null, false))
        }
}
export default authReducer
